import * as React from "react";
import Helmet from "react-helmet";
//
import { Footer } from "../components/Footer";
import { Header } from "../components/Header";
import { LayoutContainer } from "../components/LayoutContainer";
import { GatsbyImage, StaticImage, getImage } from "gatsby-plugin-image";
import { Link, graphql, useStaticQuery } from "gatsby";
import { cl } from "../helpers/general";
import { GRADIENT_1 } from "../styles";
import { SectionDescription } from "../components/SectionDescription";
import { BottomBanner } from "../components/BottomBanner";
import { OutlineLink } from "../components/OutlineLink";
import { EventCard } from "../components/EventCard";
import { HoverCard } from "../components/HoverCard";
//

const Index = () => {
  const rawData = useStaticQuery(graphql`
    query {
      allSanityEvents(limit: 4, filter: { isFeatured: { eq: true } }) {
        nodes {
          title
          subtitle
          postedAt(formatString: "D MMM YYYY")
          type
          slug {
            current
          }
          coverUrl
          cover {
            asset {
              gatsbyImageData(fit: FILL, placeholder: NONE)
            }
          }
        }
      }
      sanityHomePage {
        heroTitle
        isEventsEnabled
        isFooterBannerEnabled
        isSection1Enabled
        isSection2Enabled
        isSection3Enabled
        isSection4Enabled
        isSection5Enabled
        isSection6Enabled
        section1Description
        section1Highlight
        section2Description
        heroImage {
          asset {
            gatsbyImageData(fit: FILL, placeholder: NONE)
          }
        }
        stats {
          label
          value
        }
        section2Image {
          asset {
            gatsbyImageData(fit: FILL, placeholder: NONE)
          }
        }
        section2Items {
          label
          icon {
            asset {
              gatsbyImageData(fit: FILL, placeholder: NONE)
            }
          }
        }
        section2Prefix
        section2Title
        section3Description
        section3Prefix
        section3Title
        section4Description
        section4Items
        section4Prefix
        section4Title
        section5Description
        section5Title
        section6Title
        section6Items
        section6Image {
          asset {
            gatsbyImageData(width: 250, placeholder: NONE)
          }
        }
        section5Image {
          asset {
            gatsbyImageData(width: 400, placeholder: NONE)
          }
        }
        section4Image {
          asset {
            gatsbyImageData(fit: FILL, placeholder: NONE)
          }
        }
        section3Items {
          title
          description
          icon {
            asset {
              gatsbyImageData(width: 70, placeholder: NONE)
            }
          }
        }
      }
    }
  `);

  const events: any[] = rawData.allSanityEvents.nodes;
  const home = rawData.sanityHomePage;

  return (
    <>
      <Header />
      <LayoutContainer>
        <div className="flex flex-col md:flex-row items-center text-center md:text-left">
          <div className="space-y-10 max-w-lg">
            <div className="text-3xl md:text-6xl leading-tight mt-12 md:mt-0">
              {home.heroTitle}
            </div>
            <Link
              className="inline-block rounded-full bg-brand-one font-semibold px-11 py-4"
              // to="/explore"
              to="https://wa.me/919744062997"
            >
              Explore More
            </Link>
          </div>
          <GatsbyImage
            image={getImage(home.heroImage.asset)!}
            alt="home-hero"
            className="w-full translate-y-7 max-w-md md:max-w-none order-first sm:order-none"
          />
        </div>
      </LayoutContainer>
      {home.isSection1Enabled && (
        <LayoutContainer className="relative mt-20 md:my-32">
          <div className="md:flex items-center md:space-x-28">
            <div className="text-2xl md:text-3xl font-light leading-tight text-center md:text-left">
              <span className={cl("text-transparent bg-clip-text", GRADIENT_1)}>
                {home.section1Highlight}
              </span>
              {home.section1Description}
            </div>
            <div className="flex items-center justify-center mt-6 space-x-16 text-center">
              {home.stats.map((item: any, index: number) => {
                return (
                  <div key={index}>
                    <div className="text-5xl font-light">{item.value}</div>
                    <div className="whitespace-nowrap">{item.label}</div>
                  </div>
                );
              })}
            </div>
          </div>
        </LayoutContainer>
      )}
      {events.length > 0 && (
        <LayoutContainer className="pt-24 sm:py-12 flex items-center flex-col">
          <SectionDescription
            className="text-center"
            smallTitle="Our programs"
            bigTitle="Contest and events"
          />
          <div className="sm:grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-8 mb-12 sm:mb-16 flex overflow-x-scroll sm:overflow-x-hidden w-full">
            {events.map((item, index) => {
              return (
                <EventCard
                  key={index}
                  thumbnail={item.cover.asset}
                  title={item.title}
                  subtitle={item.subtitle}
                  link={item.slug.current}
                  type={item.type}
                  className="flex-shrink-0 w-[300px] sm:w-auto"
                />
              );
            })}
          </div>
          <OutlineLink label="Explore more" to="/events" />
        </LayoutContainer>
      )}
      {home.isSection2Enabled && (
        <LayoutContainer className="sm:grid grid-cols-2 items-center">
          <div>
            <GatsbyImage
              alt=""
              image={getImage(home.section2Image.asset)!}
              class="h-full max-w-full"
            />
          </div>
          <div className="text-center sm:text-left -mt-14 sm:mt-0">
            <SectionDescription
              smallTitle={home.section2Prefix}
              bigTitle={home.section2Title}
              className="text-center sm:text-start"
              description={home.section2Description}
            />
            <div className="grid grid-cols-2">
              {home.section2Items.map((item: any, index: number) => {
                return (
                  <div
                    key={index}
                    className="flex items-center justify-center sm:justify-start mb-8"
                  >
                    <GatsbyImage
                      image={getImage(item.icon.asset)!}
                      alt=""
                      class="w-10"
                    />
                    <div className="ml-4">{item.label}</div>
                  </div>
                );
              })}
            </div>
          </div>
        </LayoutContainer>
      )}
      {home.isSection3Enabled && (
        <LayoutContainer className="py-20 sm:pt-0 flex flex-col items-center">
          <SectionDescription
            smallTitle={home.section3Prefix}
            bigTitle={home.section3Title}
            description={home.section3Description}
            className="max-w-2xl text-center"
          />
          <div className="sm:grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-8 flex overflow-x-scroll sm:overflow-x-hidden w-full">
            {home.section3Items.map((item: any, index: number) => {
              return (
                <HoverCard key={index}>
                  <GatsbyImage alt="" image={getImage(item.icon.asset)!} />
                  <div className="mt-5 text-xl">{item.title}</div>
                  <div className="mt-8 opacity-80">{item.description}</div>
                </HoverCard>
              );
            })}
          </div>
        </LayoutContainer>
      )}
      {home.isSection4Enabled && (
        <LayoutContainer
          className="sm:grid grid-cols-2 items-center py-14"
          parentClassName="bg-gradient-to-b from-brand-four to-transparent"
        >
          <GatsbyImage
            alt=""
            image={getImage(home.section4Image.asset)!}
            class="h-full max-w-full -mt-10 sm:mt-0"
          />
          <div className="text-center sm:text-start">
            <SectionDescription
              smallTitle={home.section4Prefix}
              bigTitle={home.section4Title}
              description={home.section4Description}
            />
            <div className="space-y-3 -mt-4">
              {home.section4Items.map((item: string, index: number) => {
                return (
                  <div
                    key={index}
                    className="flex items-center justify-center sm:justify-start"
                  >
                    <StaticImage
                      src="../images/check.png"
                      class="mr-3"
                      alt=""
                    />
                    <span className="opacity-80">{item}</span>
                  </div>
                );
              })}
            </div>
          </div>
        </LayoutContainer>
      )}
      <LayoutContainer className="sm:grid grid-cols-5 sm:items-center pb-24 sm:py-24 sm:pt-0 gap-8 sm:space-y-0 flex overflow-x-scroll sm:overflow-x-hidden w-full">
        {home.isSection5Enabled && (
          <div className="bg-brand-two col-span-2 rounded-3xl p-10 sm:h-full overflow-hidden flex flex-col relative z-10 h-[460px] w-[350px] sm:w-auto min-h-[460px] flex-shrink-0">
            <div className="text-3xl">{home.section5Title}</div>
            <div className="my-4 opacity-80">{home.section5Description}</div>
            <GatsbyImage
              alt=""
              className="absolute bottom-0 right-0 -z-10"
              image={getImage(home.section5Image.asset)!}
            />
          </div>
        )}
        {home.isSection6Enabled && (
          <div className="bg-brand-three col-span-3 rounded-3xl text-black p-10 sm:h-full sm:grid grid-cols-5 overflow-hidden relative z-10 h-[460px] w-[350px] sm:w-auto flex-shrink-0">
            <div className="col-span-3">
              <div className="font-bold text-3xl">{home.section6Title}</div>
              <ul className="list-disc text-xl my-9 space-y-3">
                {home.section6Items.map((item: string, index: number) => {
                  return (
                    <li key={index} className="ml-5">
                      {item}
                    </li>
                  );
                })}
              </ul>
              <button className="rounded-full bg-black text-white font-bold px-6 py-3">
                Download App
              </button>
            </div>
            <GatsbyImage
              alt=""
              className="absolute bottom-0 -right-28 sm:right-0 -z-10 blur-xl sm:blur-none"
              image={getImage(home.section6Image.asset)!}
            />
          </div>
        )}
      </LayoutContainer>
      {home.isFooterBannerEnabled && <BottomBanner />}
      <Footer />
    </>
  );
};

export default Index;
